
import {GET_DATA_FOR_GRAP} from '../../graphql/dashboardAndReport'
import { useQuery } from "@apollo/client";
import React, { useState, useEffect } from "react";
import { Bar,Line } from "react-chartjs-2";
import { Chart } from 'react-chartjs-2';
import numeral from "numeral";
import ChartDataLabels from 'chartjs-plugin-datalabels';


function BarChart({ data, isBar }) {

let labels = data?.map(e => e._id);
let confirmArr = data?.map(e => e.confirm);
let recoveredArr = data?.map(e => e.recovered);
let deathArr =data?.map(e => e.death);

Chart.register(ChartDataLabels)

const datas = {
  labels: labels,
  datasets: [
    {
      label: "",
      data: confirmArr,
      fill: false,
      backgroundColor: "#ffd666",
      borderColor: "#ffd666",
      // color:'white'
      lineTension: 0.4,
      pointRadius: 5,
      pointBackgroundColor: "#ffd666",
      pointBorderColor: "rgba(255,255,255,0.8)",
      pointHoverRadius: 5,
      pointHoverBackgroundColor: "#ffd666",
      pointHitRadius: 50,
      pointBorderWidth: 2,
    },
    
    {
      label: "",
      data: recoveredArr,
      fill: false,
      backgroundColor: "#73d13d",
      borderColor: "#73d13d",
      // color:'white'
      lineTension: 0.4,
      pointRadius: 5,
      pointBackgroundColor: "#73d13d",
      pointBorderColor: "rgba(255,255,255,0.8)",
      pointHoverRadius: 5,
      pointHoverBackgroundColor: "#73d13d",
      pointHitRadius: 50,
      pointBorderWidth: 2,
    },
    {
      label: "",
      data: deathArr,
      fill: false,
      backgroundColor: "#cf1322",
      borderColor: "#cf1322",
      // color:'white',
      lineTension: 0.4,
      pointRadius: 5,
      pointBackgroundColor: "#cf1322",
      pointBorderColor: "rgba(255,255,255,0.8)",
      pointHoverRadius: 5,
      pointHoverBackgroundColor: "#cf1322",
      pointHitRadius: 50,
      pointBorderWidth: 2,
      
    },
  ],
  datalabels:{
    color:'blue',
    anchor:'end'

  }
};
  return (
    <>
      { isBar === "bar" ?
      <div>
          <Bar

          data={datas}
          options={{
            maintainAspectRatio: true,
            
            plugins: {
              legend: {
                  labels: {
                      // This more specific font property overrides the global property
                      font: {
                          size: 15,
                          family:"Khmer OS Siemreap"
                      },
                      color:'#e6fffb'

                  },
                  
                 
              },
              datalabels:{
                // display:false,
                color:'#FFFFFF',
                anchor:'end',
                align:'top',
                font:{
                  size:10
                }
              },
              //for chart title future use
              // title:{
              //   display:true,
              //   text:"test",
              //   font:{
              //     size:40
              //   }
              // },
              tooltip: {
                // enabled:false,
                // title:{
                //   display:true,
                //   fontSize:50
                  // font: {
                  //   size:50,
                  //   family:"Khmer OS Siemreap"
                  // }
                // }

                
                // callbacks: {
                //     labelTextSize: function(context) {
                //         return {
                //             // borderColor: 'rgb(0, 0, 255)',
                //             // backgroundColor: 'rgb(255, 0, 0)',
                //             // borderWidth: 2,
                //             // borderDash: [2, 2],
                //             // borderRadius: 2,
                //             fontSize:50,
                //         };
                //     },
                //     // labelTextColor: function(context) {
                //     //     return '#FFFFF';
                //     // }
                // }
              }
            },
            scales: {
              x: {
                  ticks: {
                      font: {
                          size: 15,
                          family:"Khmer OS Siemreap"
                      },
                      color:'#e6fffb',
                      // mirror:true,
                      // autoSkip: false,
                      // maxRotation: 90,
                      // minRotation: 90,
                      
                  },
                  // gridLines: {
                  //   color: "#FFFFFF",
                  // }
              },
              y: {
                ticks: {
                    font: {
                        size: 15,
                        family:"Khmer OS Siemreap",
                        
                    },
                    color:'#b5f5ec',
                    // mirror:true,
                    maxTicksLimit: 10
                    
                },
                // gridLines: {
                //   color: "rgba(0, 0, 0, .125)",
                // }
              }
            },

          }}
          />
      </div>
      :null
    }

    { isBar === "line" ?
      <div>
          <Line
          // height={100}
          data={datas}
          options={{
            maintainAspectRatio: true,
            
            plugins: {
              legend: {
                  labels: {
                      // This more specific font property overrides the global property
                      font: {
                          size: 15,
                          family:"Khmer OS Siemreap"
                      },
                      color:'#e6fffb'

                  },
                  
                 
              },
              datalabels:{
                // display:false,
                color:'#FFFFFF',
                anchor:'end',
                align:'top',
                font:{
                  size:10
                }
              },
              //for chart title future use
              // title:{
              //   display:true,
              //   text:"test",
              //   font:{
              //     size:40
              //   }
              // },
              tooltip: {
                // enabled:false,
                // title:{
                //   display:true,
                //   fontSize:50
                  // font: {
                  //   size:50,
                  //   family:"Khmer OS Siemreap"
                  // }
                // }

                
                // callbacks: {
                //     labelTextSize: function(context) {
                //         return {
                //             // borderColor: 'rgb(0, 0, 255)',
                //             // backgroundColor: 'rgb(255, 0, 0)',
                //             // borderWidth: 2,
                //             // borderDash: [2, 2],
                //             // borderRadius: 2,
                //             fontSize:50,
                //         };
                //     },
                //     // labelTextColor: function(context) {
                //     //     return '#FFFFF';
                //     // }
                // }
              }
            },
            scales: {
              x: {
                  ticks: {
                      font: {
                          size: 15,
                          family:"Khmer OS Siemreap"
                      },
                      color:'#e6fffb'
                      // mirror:true,
                      // autoSkip: false,
                      // maxRotation: 90,
                      // minRotation: 90,
                  }
              },
              y: {
                ticks: {
                    font: {
                        size: 15,
                        family:"Khmer OS Siemreap",
                        
                    },
                    color:'#b5f5ec'
                    // mirror:true,
                    
                }
              }
            },

          }}
          />
      </div>
      :null
    }
  </>
  );
}

export default BarChart;